<template>
	<div>
		<ContentHeader title="Resepsionis" />
		 <div class="content-header">
			<div class="container-fluid">
				<div class="card">
	              <div class="card-header">
	                <h3 class="card-title"><div>Tambah Kunjungan Pasien</div></h3>

	                <div class="card-tools">
	                  <router-link to="/tambah-data-pasien" class="btn btn-sm btn-success">Tambah pasien baru</router-link>
	                </div>
	              </div>
	              <div class="card-body" style="display: block;">
	                <form @submit.prevent="prosesKunjungan">
						<div class="row">
							<div class="col-10">
								<div class="px-lg-2">
									<div class="form-group">
					                   <label for="nomor">Nomor Antrian</label>
					                   <input type="text" class="form-control"  id="nomor" disabled="" name="nomor" placeholder="" v-model="kunjunganBaru.nomor">
						           </div>
								</div>	
							</div>
							<div class="col-2"></div>	
						</div>
						<div class="row">
							<div class="col-10">
								<div class="px-lg-2">
									<div class="form-group">
					                   <label for="pasien">Pasien</label>
					                   <select class="custom-select form-control" id="pasien" name="pasien" required="" v-model="kunjunganBaru.pasien_id">
      										<option selected></option>
	      									<option v-for="row in pasien" :key="row.id" v-bind:value="row.id">
	      										{{ row.nomor_rekam_medis }} - {{ row.nama }}
	      									</option>
	    								</select>
						           </div>
								</div>	
							</div>
							<div class="col-2">
							</div>	
						</div>
						<div class="row">
							<div class="col-10">
								<div class="px-lg-2">
									<div class="form-group">
					                   <label for="dokter">Dokter</label>
					                   <select class="custom-select form-control" id="dokter" name="dokter" required="" v-model="kunjunganBaru.dokter_id">
      										<option selected></option>
	      									<option v-for="row in dataDokter" :key="row.id" v-bind:value="row.id">
	      										Dr. {{ row.nama }}
	      									</option>
	    								</select>
						           </div>
								</div>	
							</div>
							<div class="col-2"></div>	
						</div>
						<div class="px-lg-2 mt-3">
							<button type="submit" class="btn btn-primary d-flex align-items-center">
							  <div>
							  	Submit
							  </div>	
							  <div>
			                    <svg v-if="loading" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style=" background: none; display: block; shape-rendering: auto;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
			                      <g>
			                        <path d="M50 15A35 35 0 1 0 74.74873734152916 25.251262658470843" fill="none" stroke="#ffffff" stroke-width="12"></path>
			                        <path d="M49 3L49 27L61 15L49 3" fill="#ffffff"></path>
			                        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
			                      </g>

			                    </svg>
			                  </div>
							</button>
						</div>
					</form>
    				<p class="text-center mt-2">{{cekData}}</p>
	              </div>
	              <!-- /.card-body -->
	              <div class="card-footer" style="display: block;">
	                
	              </div>
	              <!-- /.card-footer-->
	            </div>


	            <div class="card">
	              <div class="card-header">
	                <h3 class="card-title"><div>Kunjungan Hari Ini</div></h3>

	                <div class="card-tools">
	                  <button type="button" class="btn btn-tool" data-card-widget="collapse" title="Collapse">
	                    <i class="fas fa-minus"></i>
	                  </button>
	                </div>
	              </div>
	              <div class="card-body" style="display: block;">
	              	<table class="table table-bordered table-hover">
	              		<thead>
	        				    <tr>
	        				      <th scope="col" class="text-sm">No Antrian</th>
	        				      <th scope="col" class="text-sm">NAMA</th>
	        				      <th scope="col" class="text-sm">NOMOR TELEPON</th>
	        				      <th scope="col" class="text-sm">STATUS</th>
	        				      <th scope="col" class="text-sm">WAKTU KUNJUNGAN</th>
	        				    </tr>
	        				  </thead>
	        				  <tbody>
	        				  	<tr v-for="row in kunjungan" :key="row.id">
	        				  		<td class="text-sm" scope="row">{{ row.nomor }}</td>
	        				  		<td class="text-sm">{{ row.nama_pasien }}</td>
	        				  		<td class="text-sm">{{ row.nomor_telepon }}</td>
	        				  		<td class="text-sm">{{ row.status }}</td>
	        				  		<td class="text-sm">{{ row.tanggal.slice(11, 16) }}</td>
	        				  	</tr>
	        				  </tbody>
	              	</table>
	              	<p class="text-center mt-2">{{cekDataKunjungan}}</p>
	              </div>
	              <!-- /.card-body -->
	              <div class="card-footer" style="display: block;">
	                
	              </div>
	              <!-- /.card-footer-->
	            </div>

			</div>
		</div>
	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader'

	import { ref, computed, reactive, onMounted } from 'vue'
	import axios from 'axios'
	import store from '@/store'
	import Swal from 'sweetalert2'


	export default{
		components: {
			ContentHeader: ContentHeader,
		},
		setup(){
			const loading = ref(false)

			const format_member = (status) => {
				if (status == 1) {
					return 'Member'
				} else {
					return 'Belum Member'
				}
			}

			const user = computed(() => store.getters['auth/user'])
			const pasien = ref()
			const kunjungan = ref()
			const kunjunganBaru = reactive({
	    		'cabang_id' 	: user.value.cabang_id,
	    		'nomor'			: '',
	    		'pasien_id' 	: '',
	    		'dokter_id' 	: '',
	    		'status' 		: 'dalam antrian'
	    	})
			const cekData = ref()
			const cekDataKunjungan = ref()
	    	const getPasien = async () => {
	    		let response = await axios.get('api/pasien/get/' + user.value.cabang_id)
	    		
		        if (response.data == 'kosong') {
		          cekData.value = 'Data Masih Kosong'
		          pasien.value = null
		        } else {
		          cekData.value = ''
		          pasien.value = response.data
		        }
	    	}

	    	const tahun = ref()
			const bulan = ref()
			const hari = ref()

			const date = new Date()

	    	const getKunjungan = async () => {
	    		tahun.value = date.getFullYear()
				tahun.value = tahun.value.toString().slice(2, 4)
				bulan.value = date.getMonth() + 1
				bulan.value = (bulan.value > 9) ? bulan.value : '0' + bulan.value
				hari.value = date.getDate()
				hari.value = (hari.value > 9) ? hari.value : '0' + hari.value

	    		let response = await axios.get('api/kunjungan/getByDay/' + user.value.cabang_id)
		        if (response.data == 'kosong') {
		          cekDataKunjungan.value = 'Belum Ada Kunjungan'
		          kunjungan.value = null
		          kunjunganBaru.nomor = `${tahun.value}${bulan.value}${hari.value}001`
		        } else {
		          cekDataKunjungan.value = ''
		          kunjungan.value = response.data
		          let no_urut = response.data.[response.data.length - 1].nomor
		          no_urut = no_urut.slice(6, 9)
		          no_urut = parseInt(no_urut) + 1
		          no_urut = (no_urut > 9) ? (no_urut > 99) ? no_urut : '0' + no_urut : '00' + no_urut

		          kunjunganBaru.nomor = `${tahun.value}${bulan.value}${hari.value}${no_urut}`
		        }	
	    	}


	    	const dataDokter = ref()

			const getDokter = async () => {
				let response = await axios.get(`api/dokter/get/${user.value.cabang_id}`)
				
				if (response.data == 'kosong') {
		          Swal.fire({
					  title: 'Tambahkan Data Dokter!',
					  text: 'Belum Menambahkan Data Dokter',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})
		        } else {
		        	dataDokter.value = response.data
		        }
			}



	    	// Proses Kunjungan
	    	

	    	const resetKunjunganBaru = () => {
	    		kunjunganBaru.nomor = ''
	    		kunjunganBaru.pasien_id = ''
	    		kunjunganBaru.dokter_id = ''
	    	}

	    	const prosesKunjungan = () => {
	    		loading.value = true
	    		axios.post('api/kunjungan/create', kunjunganBaru)
	    		.then((response) => {
	    			if (response.data == 'sudah ada') {
	    				Swal.fire({
						  title: 'Gagal!',
						  text: 'Pasien Dalam Antrian',
						  icon: 'error',
						  confirmButtonText: 'Ok'
						})	
						loading.value = false
	    			} else {
	    				Swal.fire({
						  title: 'Berhasil!',
						  text: 'Berhasil menambahkan data',
						  icon: 'success',
						  confirmButtonText: 'Ok'
						})
						loading.value = false
	    				resetKunjunganBaru()
						getKunjungan()	
	    			}
				})
				.catch((error) => {
					Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal menambahkan data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})	
					loading.value = false
					console.log(error)
				})
	    	}
	    	// End Proses Kunjungan

	    	// Refresh data 
	    	setInterval(() => {
		    	getKunjungan()		
	    	}, 240000)
	    	// End Refresh Data



	    	onMounted(() => {
	    		getPasien(),
	    		getDokter(),
	    		getKunjungan()
	    	})

	    	return { getPasien, pasien, format_member, prosesKunjungan, getKunjungan, kunjungan, cekData, cekDataKunjungan, dataDokter, getDokter, kunjunganBaru, loading
	    	 }
		}
	}
</script>

<style>
</style>